*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.container{
  padding: 0 1rem;
}
body{
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

.btn{
  display: inline-block;
  border: none;
  padding: 7px 20px;
  color:#000;
  cursor:pointer
}
.btn:hover{
  background: #666;
}

a{
  color: #333;
  text-decoration: none;
}